}

@media only screen and (min-width: 600px) {
    header {
        display: flex;
        justify-content: space-between;
        width: 100%;
        max-height: 80px;
        background: var(--color-primary);
        box-shadow: 0px 3px 5px black;

    }

    #header-logo {
        width: 10%;
